import barTitle from '@/components/zaiui-common/basics/bar-title';
import _tool from '@/utils/tools.js';	//工具函数
export default {
	components: {
		barTitle
	},
	data() {
		return {
			goods_img: '/static/images/home/goods/10.png', scroll_top: 0, tipShow: false, chatShow: false, speedyShow: false,
			scrollHeight: 0, emojiShow: false, emojiNum: 40, emojiUrl: "emoji", toolsShow: false, emoji_swiper: 0,
		}
	},
	onLoad() {
		
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		getEmojiUrl(index) {
			//更多表情图片包下载：https://download.csdn.net/download/qq_31763107/12304958
			let num = index + 1;
			let url = "/static/images/" + this.emojiUrl + "/" + num + ".png";
			return url;
		},
		tipShowTap() {
			if(this.tipShow) {
				this.tipShow = false;
			} else {
				this.tipShow = true;
			}
		},
		chatScroll(event) {
			this.scrollHeight = event.detail.scrollHeight;
		},
		speedyTap() {
			if(this.speedyShow) {
				this.speedyShow = false;
				this.setChatShow(false);
			} else {
				this.setViewHidden();
				this.speedyShow = true;
				this.setChatShow(true);
			}
		},
		emojiSelectTap(name) {
			console.log(name);
		},
		emojiDelTap() {
			console.log('点击了删除');
		},
		emojiTap() {
			if(this.emojiShow) {
				this.emojiShow = false;
				this.setChatShow(false);
			} else {
				this.setViewHidden();
				this.emojiShow = true;
				this.setChatShow(true);
			}
		},
		toolsTap() {
			if(this.toolsShow) {
				this.toolsShow = false;
				this.setChatShow(false);
			} else {
				this.setViewHidden();
				this.toolsShow = true;
				this.setChatShow(true);
			}
		},
		setViewHidden() {
			this.speedyShow = false;
			this.emojiShow = false;
			this.toolsShow = false;
		},
		setChatShow(status){
			this.chatShow = status;
			this.scroll_top = this.scrollHeight;
		},
		setToolsHidden() {
			this.setViewHidden();
			this.setChatShow(false);
		},
		emoji_tools_tap(type) {
			this.emojiUrl = type;
			this.emoji_swiper = 0;
		},
		emoji_change(event) {
			this.emoji_swiper = event.detail.current;
		}
	}
}

